.container {position:relative; margin:80px 0 120px 0; overflow:hidden; transition:all .5s;}
.contentWrap {width:1200px; margin:0 auto; padding:50px 0 120px 0; transition:all .5s;}
.sVisualWrap {position:relative; display:flex; align-items:center; justify-content:center; width:100%; height:300px; background:url("../image/skill_bg.jpg") 50% 50% no-repeat; margin-bottom:30px; filter:grayscale(1); color:#fff; background-size:cover;}
.sVisualWrap.work {background-image:url("../image/work_bg.jpg");}
.sVisualWrap.skill {background-image:url("../image/skill_bg.jpg");}
.sVisualWrap.intro {background-image:url("../image/intro_bg.jpg"); background-position:0 95%}
.sVisualWrap h3 {position:absolute; font-size:78px; font-weight:800; letter-spacing:-0.035em; transition:all .5s; text-align:center;}
.sVisualWrap h3::before {display:block; content:''; width:80px; height:3px; background-color:#fff; position:absolute; bottom:0; left:50%; transform:translateX(-50%);}
.sVisualWrap::before {display:block; content:''; width:100%; height:100%; background:rgba(0,0,0,.75); position:absolute; left:0; top:0;}

.tab {position:relative; display:flex; overflow:hidden; border-width:1px 0; padding:0; box-sizing:border-box; font-weight:bold; margin-bottom:50px;}
.tab * {z-index:2;}
.tab div {width:25%; height:60px; text-align:center; transition: transform 0.3s ease-in; color:#fff;}
.tab div a {display:block; height:60px; line-height:60px; font-size:1.2em; color:#fff; transition: color 0s; letter-spacing:-0.05em; font-weight:400;}
.tab div a:hover {box-sizing:border-box;}
.tab div.on a {color:#000; font-weight:700;}
.glider {position: absolute; display: flex; z-index: 1; height: 100%; width: 25%; background-color: #fff; transition: 0.25s ease-out; height:60px; border-radius:30px; box-shadow: rgba(0, 0, 0, 0.1) 0px 3px 8px;}

.pjList.col3 {flex-wrap:wrap;}
.pjList.col3 li {width:calc(33.333% - 30px); margin:15px; margin-bottom:30px;}
.pjList li .imgArea {position:relative; border-radius:0 0 60px 0 / 0 0 75px 0; overflow:hidden; transition:all .5s;}
.pjList li .imgArea img {border-radius:0 0 60px 0 / 0 0 75px 0; transition:all .5s;}
.pjList li .imgArea::before {display:block; content:''; width:100%; height:100%; background-color:rgba(0,0,0,.4); opacity:0; position:absolute; transition:all .2s;}
.pjList li:hover .imgArea::before {opacity:1;}
.pjList li .txtArea {margin-top:15px;}
.pjList li .txtArea .title {font-weight:700; font-size:28px; margin-bottom:20px;}
.pjList li .txtArea .title span {font-size:18px; font-weight:400; margin-left:5px;}
.pjList li .txtArea p {display:flex; font-size:14px;}
.pjList li .txtArea p b {padding:1px 7px; border-radius:4px; margin-right:12px; min-width:70px; height:24px; display:inline-block; text-align:center; font-size:14px; background:#fff; color:#000;}
.pjList li .txtArea p + p {margin-top:10px;}
.pjList li .txtArea .color {margin-top:10px; display:flex; align-items:center;}
.pjList li .txtArea .color p {display:inline-flex; align-items:center; font-size:14px; font-weight:bold;}
.pjList li .txtArea .color p > span {display:inline-block; width:14px; height:14px; position:relative; top:1px; margin:0 6px 0 12px;} 
.pjList li .txtArea .color p + p {margin-left:10px; margin-top:0;}
.pjList li .txtArea .row {display:flex; align-items:center;}
.pjList li .txtArea .row.f_start {align-items:flex-start; word-break:keep-all; margin-top:10px;}
.pjList li .txtArea .row.f_start div {display:flex; flex-wrap:wrap; align-items:center; width:100%; font-size:14px;}
.pjList li .txtArea .row.f_start div + div {margin-top:7px;}
.pjList li .txtArea .row.f_start div img {margin-right:5px; width:20px; height:20px;}
.pjList li .btnGroup {margin-top:60px;}


.listWrap {position:relative;}
.listWrap .contentWrap {width:900px;}
.list {position:relative;}
.list::before {display:block; content:''; width:100px; height:1px; background-color:#fff; position:absolute; left:-130px; top:25px;}
.list .titleWrap .title {position:relative; font-weight:700; font-size:48px; line-height:1; margin-bottom:30px;}
.list .titleWrap .title span {font-size:24px; font-weight:500; margin-left:10px;}
.list .listSwiper {position:relative; overflow:hidden; padding-bottom:30px;}
.list .listSwiper .swiper-pagination-progressbar {position:absolute; top:auto; bottom:0; height:3px; background-color:#555;}
.list .listSwiper .swiper-pagination-progressbar-fill {background:#c9c9c9; height:3px;}
.list .listSwiper .swiper-button-prev, .list .listSwiper .swiper-button-next {width:85px; height:85px; background-size:85px;}
.list .listSwiper .swiper-button-prev {background-image:url("../image/button/list_arrow_left.png"); left:-100px;}
.list .listSwiper .swiper-button-next {background-image:url("../image/button/list_arrow_right.png"); right:-100px;}
.list .imgArea {position:relative; width:100%; max-height:500px; overflow:hidden;}
.list .infoArea {margin-top:50px; margin-bottom:80px;}
.list .infoArea li .title {font-size:24px; margin-bottom:5px;}
.list .infoArea li + li {margin-top:25px;}
.list .infoArea li p {display:flex; color:#c9c9c9;}
.list .infoArea li p span {display:inline-block; min-width:100px; color:#9b815d; font-weight:600;}
.list .infoArea li p b {font-weight:400; color:#c9c9c9;}
.list .infoArea li p a {word-break:break-all;}

.listWrap .videoBg {position:absolute; width:100vw; height:100%; opacity:.35; left:0; top:0;}
.listWrap .videoBg video {width:100%; height:100%;}

.introBg {position:fixed; bottom:-15px; width:100%; animation: marquee 52s linear infinite; display:flex; filter:grayscale(.6);}
.introBg h3 {position:relative; width:100%; background-image: url("../image/timeline.jpg"); background-repeat: no-repeat; background-size: auto; background-position: 88% 72%; background-clip: text; -webkit-background-clip: text; color: transparent; font-weight:800; font-size:120px; text-align:right; line-height:1;}
.introBg h3::before {display:block; content:"INTRODUCE"; width:100%; height:100%; text-shadow:8px 8px 0 rgba(50,50,50,1); position:absolute; left:0; top:0; z-index:-1;}
.introBg h3 + h3 {margin-left:60px;}

.introWrap .introbox {position:relative; height:240px; margin-bottom:70px;}
.introWrap .introbox .me {position:relative; top:0; left:50%; transform:translateX(-50%); width:192px; height:240px; background:url("../image/3.png"); animation: cg 2s infinite; animation-timing-function:step-end; transition:all 1s; filter:grayscale(.4); margin-top:50px;}
.introWrap .introbox .me::before {display:flex; justify-content:center; align-items:center; content:'üñê'; width:40px; height:40px; background:#ddd; position:relative; top:-60px; color:#000; border-radius:50%; margin:0 auto; animation: shaking 3s infinite; animation-timing-function:step-end;}

.skillList {overflow:hidden; padding-bottom:100px;}
.skillList .title {font-size:3.4em; font-weight:800; margin-bottom:50px; letter-spacing:-0.1em;}
.skillList .swiper-wrapper .box {border-radius:15px; background-color:#fff; overflow:hidden;}
.skillList .swiper-wrapper .box div {padding:25px 20px;}
.skillList .swiper-wrapper .box .topArea {padding:30px 20px;}
.skillList .swiper-wrapper .box.dsh .topArea {background:linear-gradient(110deg,#00a3d4 50%,#00c3ff 99%);}
.skillList .swiper-wrapper .box.hrdk .topArea {background:linear-gradient(109deg,#276ff9 50%,#5992ff 99%);}
.skillList .swiper-wrapper .box.kpc .topArea {background:linear-gradient(116deg,#ef405a 46%,#f95e75 103%);}
.skillList .swiper-wrapper .box .topArea .tit {font-size:1.1em; font-weight:700; margin-bottom:50px;}
.skillList .swiper-wrapper .box .topArea p:not(.tit) {font-size:.8em;}
.skillList .swiper-wrapper .box .topArea p:last-child {opacity:.7;}
.skillList .swiper-wrapper .box .bottomArea {position:relative; color:#000;}
.skillList .swiper-wrapper .box .bottomArea p {font-size:.8em;}
.skillList .swiper-wrapper .box .bottomArea p + p {margin-top:3px;}
.skillList .swiper-wrapper .box .bottomArea p span {min-width:45px; display:inline-block; font-weight:600;}

.contactArea .title {font-size:3.4em; font-weight:800; letter-spacing:-0.1em;}
.contactArea .btnGroup {display:flex;}
.contactArea .btnGroup a {width:100%; display:flex; align-items:center; justify-content:center;}
.contactArea .btnGroup a + a {margin-left:30px;}
.contactArea .btnGroup a img {width:28px; margin-right:10px;}

.bounce span {display:inline-block; animation: bounce 1.5s infinite;}
.bounce span:nth-child(1) {animation-delay: .1s}
.bounce span:nth-child(2) {animation-delay: .2s}
.bounce span:nth-child(3) {animation-delay: .3s}
.bounce span:nth-child(4) {animation-delay: .4s}
.bounce span:nth-child(5) {animation-delay: .5s}
.bounce span:nth-child(6) {animation-delay: .6s}
.bounce span:nth-child(7) {animation-delay: .7s}

@keyframes bounce {
    0%{
        transform: translateY(0);
    }
    50%{
        transform: translateY(-15px);
    }
	100%{
        transform: translateY(0);
    }
}


@-webkit-keyframes cg {
  0% {
    background:url("../image/2.png");
  }
  15% {
    background:url("../image/3.png");
  }
  30% {
   background:url("../image/3.png");
  }
  70% {
   background:url("../image/3.png");
  }
  85% {
   background:url("../image/2.png");
  }
  100% {
   background:url("../image/2.png");
  }
}

@keyframes shaking {
  0% {transform: translate(0, 0) rotate(0deg);}
  5% {transform: translate(5px, 5px) rotate(5deg);}
  10% {transform: translate(0, 0) rotate(0eg);}
  15% {transform: translate(-5px, 5px) rotate(-5deg);}
  20% {transform: translate(0, 0) rotate(0deg);}
  25% {transform: translate(5px, 5px) rotate(5deg);}
  30% {transform: translate(0, 0) rotate(0eg);}
  35% {transform: translate(-5px, 5px) rotate(-5deg);}
  40% {transform: translate(0, 0) rotate(0deg);}
}

.introWrap .introtext {text-align:center;}
.introWrap .introtext .title {font-size:2.6em; font-weight:800; letter-spacing:.05em; line-height:1.4; margin-bottom:20px; word-break:keep-all;}
.introWrap .introtext p {font-size:1.5em; word-break:keep-all;}

.scrollBanner {position: relative; padding-bottom: 26.25%; margin-bottom:50px;}
.scrollBanner__image {width: 100%; height: 100%;}
.afterImage {position: absolute; overflow: hidden; top: 0; transform: translate(100%, 0px);}
.afterImage img {transform: translate(-100%, 0px);}
.scrollBanner__image img {width: 100%; height: 100%; position: absolute; top: 0;}



@media screen and (max-width: 1400px){
	.container {margin-top:70px;}
}

@media screen and (max-width: 1200px){
	.contentWrap {width:100%; padding:50px 40px 0 40px;}
	.tab div a {font-size:1.1em;}

	.pjList.col3 {display:flex;}
	.pjList.col3 li {margin:15px 10px; width:calc(33.333% - 20px);}
}

@media screen and (max-width: 1024px){
	.container {margin-top:60px;}
	.contentWrap {padding:50px 30px 50px 30px;}
	.sVisualWrap {margin-bottom:0;}
	.sVisualWrap h3 {font-size:3.6em;}
	.tab {overflow:visible; margin-bottom:30px;}
	.tab div, 
	.tab div a {height:50px; line-height:50px;}
	.glider {height:50px; border-radius:0;}

	.pjList.col3 li {width:calc(50% - 20px);}
	.pjList li .imgArea img {width:100%;}

	.pjList li .txtArea .title {font-size:1.6em; line-height:1.1;}
	.pjList li .txtArea .color p + p {margin-left:0;}
	.pjList li .txtArea .color p {margin-right:10px;}

	.listWrap .contentWrap {width:100%;}
	.list .titleWrap .title {font-size:2em;}
	.list .infoArea {margin-top:40px; margin-bottom:60px;}
	.list .infoArea li .title {font-size:1.4em;}

	.introWrap .introbox {display:none;}

	.contactArea .btnGroup a + a {margin-left:15px;}
}

@media screen and (max-width: 768px){
	.contentWrap {padding:50px 25px 50px 25px;}

	.pjList li .imgArea,
	.pjList li .imgArea img {border-radius:0 0 45px 0 / 0 0 45px 0}

	.pjList li .txtArea p b {min-width:65px; margin-right:8px; font-size:13px; height:20px; line-height:20px;}
	.pjList li .txtArea .color p {margin-right:8px;}

	.skillList .title {margin-bottom:10px;}
	.contactArea .btnGroup {flex-wrap:wrap;}
	.contactArea .btnGroup a + a {margin-left:0; margin-top:15px;}
}

@media screen and (max-width: 640px){

	.sVisualWrap {height:200px;}
	.sVisualWrap h3 {font-size:2.4em;}
	.tab div a {padding:0 10px; height:45px; line-height:normal; display:flex; justify-content:center; align-items:center; word-break:keep-all; font-size:1em;}
	.glider {height:45px;}

	.pjList.col3 {border-top:1px solid #555;}
	.pjList.col3 li {width:100%; margin:0;}
	.pjList li .imgArea, .pjList li .imgArea img {display:none;}
	.pjList li .txtArea {margin:15px 0;}
	.pjList li .txtArea .title {margin-bottom:15px;}
	.pjList.col3 li {border-bottom:1px solid #555;}

	.pjList li.pc_design .imgArea, .pjList li.pc_design .imgArea img,
	.pjList li.pc_etc .imgArea, .pjList li.pc_etc .imgArea img {display:block; width:80%; margin:0 auto;}
	.pjList li.pc_design,
	.pjList li.pc_etc {margin-top:15px;}
	.pjList li.pc_design .txtArea,
	.pjList li.pc_etc .txtArea {text-align:center;}
	.pjList li.pc_design .txtArea .row.f_start,
	.pjList li.pc_etc .txtArea .row.f_start {justify-content:center;}
	.pjList li.pc_design .txtArea .row.f_start div,
	.pjList li.pc_etc .txtArea .row.f_start div {width:auto;}

	.introWrap .introtext .title {font-size:1.8em;}
	.introWrap .introtext p {font-size:1.2em;}
}